<template lang="jade">
    div
        .spinner
            .double-bounce1
            .double-bounce2
            .text Loading...
</template>
<script>
export default {
    name: "loading",
    mounted: function() {

    },
    methods: {

    }
}
</script>
<style scoped lang="scss">
.spinner {
    width: 60px;
    height: 60px;
    position: relative;
    margin: 100px auto;
    .text{
        position: absolute;
        color: #999;
        top: 70px;
    }
}

.double-bounce1,
.double-bounce2 {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: #c00000;
    opacity: 0.6;
    position: absolute;
    top: 0;
    left: 0;

    -webkit-animation: bounce 2.0s infinite ease-in-out;
    animation: bounce 2.0s infinite ease-in-out;
}

.double-bounce2 {
    -webkit-animation-delay: -1.0s;
    animation-delay: -1.0s;
}

@-webkit-keyframes bounce {
    0%,
    100% {
        -webkit-transform: scale(0.0)
    }
    50% {
        -webkit-transform: scale(1.0)
    }
}

@keyframes bounce {
    0%,
    100% {
        transform: scale(0.0);
        -webkit-transform: scale(0.0);
    }
    50% {
        transform: scale(1.0);
        -webkit-transform: scale(1.0);
    }
}
</style>



